.tab-wrap {
    background: White;
    -webkit-box-shadow: 0 0 5px rgba(0,0,0,.1);
            box-shadow: 0 0 5px rgba(0,0,0,.1);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    overflow: hidden;
    padding: 0 0 20px;
  }
.tab-label {
    color: Gray;
    cursor: pointer;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    font-weight: bold;
    -webkit-box-ordinal-group: 0;
        -ms-flex-order: -1;
            order: -1;
    padding: 12px 6px;
    position: relative;
    text-align: center;
    -webkit-transition: cubic-bezier(0.4, 0, 0.2, 1) .2s;
    transition: cubic-bezier(0.4, 0, 0.2, 1) .2s;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    white-space: nowrap;
    -webkit-tap-highlight-color: transparent;
  }
.tab-label:hover {
    background: rgba(0, 191, 255,.1);
  }
.tab-switch:checked + .tab-label {
    color: DeepSkyBlue;
  }
.tab-label::after {
    background: DeepSkyBlue;
    bottom: 0;
    content: '';
    display: block;
    height: 3px;
    left: 0;
    opacity: 0;
    pointer-events: none;
    position: absolute;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    -webkit-transition: cubic-bezier(0.4, 0, 0.2, 1) .2s 80ms;
    transition: cubic-bezier(0.4, 0, 0.2, 1) .2s 80ms;
    width: 100%;
    z-index: 1;
  }
.tab-switch:checked ~ .tab-label::after {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
.tab-switch:checked + .tab-label::after {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
.tab-content {
    height:0;
    opacity:0;
    padding: 0 0px;
    pointer-events:none;
    -webkit-transform: translateX(-30%);
            transform: translateX(-30%);
    -webkit-transition: opacity .3s 80ms, -webkit-transform .3s 80ms;
    transition: opacity .3s 80ms, -webkit-transform .3s 80ms;
    transition: transform .3s 80ms, opacity .3s 80ms;
    transition: transform .3s 80ms, opacity .3s 80ms, -webkit-transform .3s 80ms;
    width: 100%;
  }
.tab-switch:checked ~ .tab-content {
    -webkit-transform: translateX(30%);
            transform: translateX(30%);
  }
.tab-switch:checked + .tab-label + .tab-content {
    height: auto;
    opacity: 1;
    -webkit-box-ordinal-group: 2;
        -ms-flex-order: 1;
            order: 1;
    pointer-events:auto;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
.tab-wrap::after {
    content: '';
    height: 20px;
    -webkit-box-ordinal-group: 0;
        -ms-flex-order: -1;
            order: -1;
    width: 100%;
  }
.tab-switch {
    display: none;
  }
